import {
  Component,
  ComponentRef,
  EventEmitter,
  HostBinding,
  Injector,
  input,
  Input,
  Output,
  Provider,
  signal,
  Type,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReplaySubject } from 'rxjs';
import { MODAL_REF, ModalRefApi } from '../../services';
import {
  dropdownAnimation,
  DropdownsAnimationDirective,
} from '../../directives';

@Component({
  selector: 'custom-expanding-container',
  standalone: true,
  imports: [DropdownsAnimationDirective],
  templateUrl: './custom-expanding-container.component.html',
  styleUrls: ['./custom-expanding-container.component.css'],
  animations: [dropdownAnimation],
})
export class CustomExpandingContainerComponent {
  @Input() expanded = false;
  @Input() showIcon1 = true;
  @Input() padding = '2.3em 2.4em';
  @Input() borderRadius = '1em';
  @Input() backgroundColor = '#ffffff';
  @Input() showBorderBottom = true;
  @Input() minMaxHeight = '8em';

  @Output() toggled: EventEmitter<boolean> = new EventEmitter<boolean>();

  isExpanded = signal(false);

  @HostBinding('style.--min-max-height') minMaxHeightCss = this.minMaxHeight; // e.g. "420px" or "60vh"

  ngOnInit() {
    this.isExpanded.set(this.expanded);
  }

  toggle(): void {
    this.isExpanded.update((value) => !value);
    this.toggled.emit(this.isExpanded());
  }

  open(): void {
    this.isExpanded.set(true);
  }

  close(): void {
    this.isExpanded.set(false);
  }
}
