import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CustomFileUploadComponent } from '../custom-file-upload/custom-file-upload.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ISmpAttachmentAfterResolving } from '../../interfaces';


@Component({
  selector: 'custom-smp-file-upload',
  imports: [FormsModule, ReactiveFormsModule, CommonModule],
  templateUrl: './custom-smp-file-upload.component.html',
  styleUrl: './custom-smp-file-upload.component.css'
})
export class CustomSmpFileUploadComponent extends CustomFileUploadComponent {

   // NEW: Edit mode & existing items
  @Input() editMode: boolean = false;
  @Input() existingItems: ISmpAttachmentAfterResolving[] = []; // already resolved SafeUrl (from parent)
  @Output() removedExistingIds = new EventEmitter<string[]>();
    private removedExisting = new Set<string>();

      isEditMode(): boolean {
    return this.editMode && this.existingItems && this.existingItems.length > 0;
  }

  existingPreviewSrc(item: ISmpAttachmentAfterResolving): any {
    // SafeUrl comes from parent
    return item.imagePath;
  }
 removeExistingAt(index: number): void {
    const item = this.existingItems[index];
    if (!item) return;
    this.removedExisting.add(item.id);
    this.existingItems = [
      ...this.existingItems.slice(0, index),
      ...this.existingItems.slice(index + 1),
    ];
    this.emitRemovedExisting();
  }
   private emitRemovedExisting(): void {
    this.removedExistingIds.emit(Array.from(this.removedExisting));
  }


}
